.mint-heading {
  font-family: "Sharpie-Bold", sans-serif;
  font-style: italic;
  font-weight: 500;
  font-size: 48px;
  line-height: 63px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 0;
}

.mint-desctiption {
  font-family: "Sharpie-Bold", sans-serif;
  margin-top: 0;
  font-style: italic;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 10px;
}

.mint-balance {
  margin-top: 0;
  // font-style: italic;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  text-align: center;
}

.mint-balance-buy {
  font-style: italic;
  color: #ffe200;
}

.mint-wrap {
  margin-top: 5%;
}

.mint-input-wrap {
  margin-top: 20px;
}

.input-wrap {
  margin-left: -30px;
}

.mint-input {
  width: 150px;
  background: #343434;
  border-radius: 5px;
  border: none;
  box-shadow: none;
  outline: none;
  margin-right: 10px;
  margin-left: 10px;
  height: 35px;
  padding: 0 10px;

  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #ffffff;
}

.input-text {
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
}

.button-desctiption,
.button-desctiption {
  font-family: "Helvetica Neue";
  margin-top: 15px;
  font-style: italic;
  font-weight: 100;
  font-size: 16px;
  margin-bottom: 0;
  line-height: 18px;
  color: #ffffff;
  text-align: center;
}

.button-desctiption-2 {
  margin-top: 5px;
}

.mint-numbers {
  justify-content: space-between;
  width: 170px;
  margin-top: 8px;
  :hover {
    background: #ffe200;
    color: black;
  }
  // margin-left: 40px;
}

.mint-numbers-item {
  padding: 5px 10px;
  background: #1c1c1c;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  font-family: "Sharpie", sans-serif;

  :hover {
    background: #ffe200;
  }
}

.mint-button-wrap {
  margin-top: 50px;
}
